html,
body {
  height: 100%;
}
body {
  font-size: 14px;
  background-color: #f1f1f1;
  min-width: 1280px;
  position: relative;
}
body,
ul,
li {
  margin: 0;
  padding: 0;
}
ul {
  list-style-type: none;
}
.btn {
  text-decoration: none;
  color: #fff;
  background-color: #213e97;
  padding: .4em 1.2em;
}
.btn:hover {
  background-color: #182d6d;
}
.btn:active {
  background-color: #0f1c43;
}
.btn.btn_stage {
  background-color: #f69506;
}
.btn.btn_stage:hover {
  background-color: #c47705;
}
.btn.btn_stage:active {
  background-color: #925904;
}
.page_ct {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.page_ct > .page_top {
  width: 100%;
  height: 50px;
  background: #2f3ba2;
  display: flex;
  color: #fff;
  justify-content: space-between;
  align-items: center;
}
.page_ct > .page_top .top_left {
  display: flex;
  align-items: center;
  font-size: 18px;
}
.page_ct > .page_top .top_left .top_logo {
  width: 100px;
  height: 50px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.page_ct > .page_top .top_right {
  display: flex;
  align-items: center;
}
.page_ct > .page_top .top_right .top_bg_3colors {
  width: 100px;
  height: 50px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.page_ct > .page_top .top_right .oper_list {
  display: flex;
  padding-left: 50px;
  padding-right: 30px;
}
.page_ct > .page_top .top_right .oper_list .oper_item {
  display: flex;
  margin: 0 15px;
  align-items: center;
  cursor: pointer;
}
.page_ct > .page_top .top_right .oper_list .oper_item .icon {
  margin-right: 10px;
}
.page_ct > .page_top .top_right .oper_list .oper_item.oper_user {
  position: relative;
}
.page_ct > .page_top .top_right .oper_list .oper_item.oper_user .user_num {
  background-color: #fff;
  position: absolute;
  top: -10px;
  right: -15px;
  color: #2f3ba2;
  font-size: 12px;
  padding: 0 3px;
  height: 12px;
  line-height: 12px;
  text-align: center;
  border-radius: 3px;
}
